body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

* {
  box-sizing: border-box;
}

.App {
  font-family: consolas, monaco, 'Inconsolata', monospace;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}
pre {
  background-color: #ececec;
  padding: 10px;
  overflow: auto;
}
td,
#customers th {
  border: 1px solid #ddd;
  padding: 8px;
}

tr:nth-child(even) {
  background-color: #f2f2f2;
}

tr:hover {
  background-color: #ddd;
}

th {
  padding: 12px;
  text-align: left;
  background-color: #222;
  color: white;
}

.Cards-wrapper {
  margin-top: 150px;
}
@media only screen and (max-width: 700px) {
  .Cards-wrapper {
    margin-top: 130px;
    flex: 1 1;
  }
}
.Cards {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 100px;
}
.swal-content {
  text-align: left;
  overflow: auto;
  max-height: calc(80vh - 110px);
}
.swal-content * {
  font-family: monaco, consolas, 'Inconsolata', monospace;
}
.swal-modal {
  width: 90% !important;
  max-height: 80vh;
  max-width: 900px;
}
.swal-button {
  background-color: #f5f5f5 !important;
  color: #222;
}
.swal-button:hover {
  background-color: #f5f5f5 !important;
}

.no-content {
    width: 100%;
    height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.no-content img {
  margin-top: 50px;
  width: 50%;
  margin-bottom: 50px;
}

.Card {
  background-color: #f5f5f5;
  border: 1px solid #eeeeee;
  padding: 10px 20px;
  margin: 10px;
  border-radius: 5px;
  box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.08);
  width: 300px;
  height: 100px;
  display: flex;
  flex-direction: column;
  -webkit-transition: 0.3s all;
  transition: 0.3s all;
  cursor: pointer;
}

.Card:hover {
  background-color: #f9f9f9;
  -webkit-transform: translateY(-4px);
          transform: translateY(-4px);
  box-shadow: 0 7px 14px rgba(50,50,93,.1), 0 3px 6px rgba(0,0,0,.08);
}

.header {
  text-align: right;
  text-transform: uppercase;
}
.title {
  flex: 1 1;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #222;
}

.Footer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-wrap: wrap;
  background-color: #222;
  color: white;
  padding: 10px 30px;
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 100;
}

.Footer > span > a {
  color: turquoise;
}

@media only screen and (max-width: 600px) {
  .Footer {
    display: none;
  }
}

.Header-wrapper {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #fff;
  box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.08);
  z-index: 100;
}
.Header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 40px;
  flex-wrap: wrap;
}
.search {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f5f5f5;
  padding: 10px 20px;
  border-radius: 5px;
  width: 300px;
}
@media only screen and (max-width: 700px) {
  .Header {
    padding: 5px 0;
    justify-content: center;
  }
  .Header-hero {
    margin: 10px;
    width: 100%;
    text-align: center;
    font-size: 1.5rem;
  }
  .search {
    width: 100%;
    padding: 10px;
    border-radius: 0;
  }
}
.Header-icon {
  height: 20px;
  margin-right: 10px;
}
.Header-input {
  border: none;
  outline: none;
  background-color: #f5f5f5;
  font-size: 1rem;
  flex: 1 1;
}
.Header-boundry {
  background: #cccccc1a;
  border: 3px solid #cccccc61;
  border-radius: 5px;
  margin: 20px 100px;
}

h2 {
  text-align: center;
}

